/* VARIABLES */
:root {
  /* Base background color */
  --app-background-base-color: rgb(27, 28, 31);

  /* Background color variations (Generated at: www.colorbook.io) */
  --app-background-5-lighter-color: rgb(39, 40, 45);
  --app-background-10-lighter-color: rgb(51, 53, 58);
  --app-background-15-lighter-color: rgb(63, 65, 72);
  --app-background-5-darker-color: rgb(15, 16, 17);
  --app-background-10-darker-color: rgb(3, 3, 4);
  --app-background-15-darker-color: rgb(0, 0, 0);

  /* Base text color */
  --app-text-base-color: rgb(242, 242, 242);

  /* Text color variations */
  --app-text-5-lighter-color: rgb(255, 255, 255);
  --app-text-5-darker-color: rgb(229, 229, 229);
  --app-text-10-darker-color: rgb(216, 216, 216);
  --app-text-15-darker-color: rgb(203, 203, 203);
  --app-text-40-darker-color: rgb(138, 138, 138);

  /*  */
  --app-success-color: rgb(76, 174, 79);
  --app-danger-color: rgb(207, 63, 33);
}

/* RESET */
*,
*:before,
*:after {
  box-sizing: border-box;
}

html,
body {
  margin: 0;
  padding: 0;
  font-family: Roboto, Lato;
}

body {
  background-color: var(--app-background-base-color);
  color: var(--app-text-base-color);
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-rendering: optimizeLegibility;
}
